.spec {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.spec__title h2 {
  font-weight: 700;
  font-size: 4em;
}
.spec__card-list {
  display: flex;
  padding: 3rem;
  overflow-x: auto;
}
@media (max-width: 1200px) {
  .spec__card-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
}
.spec__card-list::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
.spec__card-list::-webkit-scrollbar-thumb {
  background: #201c29;
  border-radius: 10px;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, 0.25), inset -2px -2px 2px rgba(0, 0, 0, 0.25);
}
.spec__card {
  display: flex;
  position: relative;
  flex-direction: column;
  height: 350px;
  width: 400px;
  min-width: 250px;
  padding: 1.5rem;
  border-radius: 16px;
  background: #121212;
  box-shadow: -1rem 0 3rem #000;
  transition: 0.2s;
}
.spec__card:hover {
  transform: translateY(-2rem);
}
.spec__card:hover ~ .spec__card {
  transform: translateX(90px);
}
.spec__card:not(:first-child) {
  margin-left: -130px;
}
@media (max-width: 1200px) {
  .spec__card:not(:first-child) {
    margin-left: 0;
    margin-top: 2rem;
  }
}
.spec__card h1 {
  color: #FD5722;
  font-weight: 700;
  margin-bottom: 0;
  text-transform: uppercase;
}
.spec__card ul li {
  font-size: 1.2em;
  font-weight: 500;
}